//   accounts.reverse().map(account => {
//     if (!account.has_child && account.level !== 1) {
//       total_beginning += parseFloat(account.opening_balance_account);
//     } else if (account.has_child && account.level !== 1) {
//       account.opening_balance_account = total_beginning;
//     } else if (account.has_child && account.level === 1) {
