class ChartOfAccountReport extends Component {
  state = {
    filter: {
      gl_type: null
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getChartOfAccountReports } = this.props;
    getChartOfAccountReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.chartOfAccountReport.error) {
      notification("error", this.props.chartOfAccountReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.chartOfAccountReport.success) {
      notification("success", this.props.chartOfAccountReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "display_parent") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value.target.value
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.gl_type) {
      string
        ? (string += "&gl_type=" + filter.gl_type)
        : (string += "gl_type=" + filter.gl_type);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getChartOfAccountReports } = this.props;
    //const { current_page } = this.props.chartOfAccountReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getChartOfAccountReports(1, null, filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getChartOfAccountReports } = this.props;
    this.setState({
      filter: {
        gl_type: null
      }
    });
    getChartOfAccountReports();
  };

  hitungTotalAmountParent = (array_ori, array_filter) => {
    array_filter.map(data => {
      if (data.has_child === 1) {
        let filter = array_ori.filter(
          x => x.id_account_parent === data.id_account
        );
        if (filter.length > 0) {
          this.hitungTotalAmountParent(array_ori, filter);
        }
      } else if (data.id_account_parent === null) {
        console.log("Ya");
      } else {
        let parent = array_ori.find(
          x => x.id_account === data.id_account_parent
        );
        array_ori.map(x => {
          if (x.id_account === parent.id_account) {
            let parent2 = parent;
            while (parent2 !== undefined) {
              parent2.current_balance += parseFloat(data.current_balance);
              parent2 = array_ori.find(
                y => y.id_account === parent2.id_account_parent
              );
              //console.log(parent2)
            }
          }
        });
      }
    });
  };

  modifyData = data => {
    let dataSource = [];
    let dataExcel = [];
    if (Array.isArray(data) && data.length > 0) {
      data.map(coa => {
        let row = Object.assign({}, coa);
        row.key = row.id_account;
        row.opening_balance =
          parseFloat(row.opening_balance) * parseFloat(row.ob_rate);

        if (row.account_type && row.account_type.normal_balance === 1) {
          row.current_balance =
            parseFloat(row.account_balance.debit_balance) -
            parseFloat(row.account_balance.credit_balance);
        } else if (row.account_type && row.account_type.normal_balance === 2) {
          row.current_balance =
            parseFloat(row.account_balance.credit_balance) -
            parseFloat(row.account_balance.debit_balance);
        }

        if (row.has_child === 1) {
          row.current_balance = 0;
        }

        // nggo benakke sing exponensiale akeh (bodol pancen'e)
        // row.current_balance = parseFloat(row.current_balance.toFixed(6));

        row.opening_balance_date = row.opening_balance_date
          ? row.opening_balance_date
          : row.created_at;

        dataSource.push(row);
        //console.log(row.account_name, row.current_balance)
      });
    }

    let level_1 = dataSource.filter(x => x.level === 1);
    this.hitungTotalAmountParent(dataSource, level_1);

    dataSource.map(coa => {
      let row = Object.assign({}, coa);
      dataExcel.push(
        Object.assign({}, row, {
          account_code:
            row.level > 1
              ? " ".repeat(row.level * 3) + row.account_code
              : row.account_code,
          account_name:
            row.level > 1
              ? " ".repeat(row.level * 3) + row.account_name
              : row.account_name
        })
      );
    });

    return { dataSource, dataExcel };
  };

  render() {
    // console.log(this.props.chartOfAccountReport);
    // Data setup
    const { chart_of_accounts_reports, gl_types, loading } = this.props
      .chartOfAccountReport
      ? this.props.chartOfAccountReport
      : [];
    //console.log(this.props.chartOfAccountReport.base_currency); //console.log(this.state);
    const modifiedData = this.modifyData(chart_of_accounts_reports);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "accountCode" }),
        dataIndex: "account_code",
        rowKey: "account_code",
        width: "12%",
        render: (text, row) => {
          if (row.has_child) {
            if (row.level > 1) {
              return (
                <strong style={{ marginLeft: `${row.level * xx}px` }}>
                  {text}
                </strong>
              );
            } else {
              return <strong>{text}</strong>;
            }
          } else if (row.level === 1) {
            return text;
          } else {
            return (
              <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
            );
          }
        }
      },
      {
        title: getLanguage({ id: "accountName" }),
        dataIndex: "account_name",
        rowKey: "account_name",
        width: "25%",
        render: (text, row) => {
          if (row.has_child) {
            if (row.level > 1) {
              return (
                <strong style={{ marginLeft: `${row.level * xx}px` }}>
                  {text}
                </strong>
              );
            } else {
              return <strong>{text}</strong>;
            }
          } else if (row.level === 1) {
            return text;
          } else {
            return (
              <span style={{ marginLeft: `${row.level * xx}px` }}>{text}</span>
            );
          }
        }
      },
      {
        title: getLanguage({ id: "accounts.type" }),
        dataIndex: "account_type.account_type_name",
        rowKey: "account_type.account_type_name",
        width: "12%",
        render: (text, row) => {
          if (row.has_child) {
            return <strong>{text}</strong>;
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "currency" }),
        dataIndex: "currency.currency_code",
        rowKey: "currency.currency_code",
        width: "4%",
        render: (text, row) => {
          if (row.has_child) {
            return <strong>{text}</strong>;
          } else {
            return text;
          }
        }
      },
      // {
      //   title: "Description",
      //   dataIndex: "description",
      //   rowKey: "description",
      //   width: "31%",
      //   render: (text, row) => {
      //     if (row.has_child) {
      //       return <strong>{text}</strong>;
      //     } else {
      //       return text;
      //     }
      //   }
      // },
      // {
      //   title: "Opening Balance (IDR)",
      //   dataIndex: "opening_balance",
      //   rowKey: "opening_balance",
      //   align: "right",
      //   width: "15%",
      //   parse: true,
      //   render: (text, row) => {
      //     const str = text
      //       ? <NumberFormat value={text} />
      //       : "0";
  
      //     if (row.has_child) {
      //       return <strong>{str}</strong>;
      //     } else {
      //       return str;
      //     }
      //   }
      // },
      // {
      //   title: "As Of",
      //   dataIndex: "opening_balance_date",
      //   rowKey: "opening_balance_date",
      //   width: "16%",
      //   date: true,
      //   render: (text, row) => {
      //     const str = text
      //       ? moment(text).format("DD MMMM YYYY HH:mm:ss")
      //       : moment(row.created_at).format("DD MMMM YYYY HH:mm:ss");
      //     if (row.has_child) {
      //       return <strong>{str}</strong>;
      //     } else {
      //       return str;
      //     }
      //   }
      // },
      {
        title: `${getLanguage({ id: "balance" })} (IDR)`,
        dataIndex: "current_balance",
        rowKey: "current_balance",
        width: "15%",
        align: "right",
        parse: true,
        render: (text, row) => {
          // if (row.has_child) {
          //   if (row.level > 1) {
          //     return null;
          //   } else {
          //     return null;
          //   }
          // }
  
          const str = <NumberFormat value={text} />;
  
          if (row.has_child) {
            return <strong>{str}</strong>;
          } else {
            return str;
          }
        }
      }
    ];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const fileName = getLanguage({ id: "chartOfAccountReports.pageHeader" })

    // if (this.state.filter.display_parent) {
    //   getTotalParent(dataSource);
    // }

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="chartOfAccountReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="chartOfAccountReports.pageHeader" />}
              columns={columns}
              data={modifiedData.dataSource}
              filter={this.state.filter}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
            />

            <PDFWrapper
              columns={columns}
              data={modifiedData.dataSource}
              fileName={fileName}
              // filter={this.state.filter}
              landscape={true}
              loading={loading}
              margin="1cm"
              scale={0.7}
              title="chartOfAccountReports.pageHeader"
            />

            <ExcelWrapper
              title="chartOfAccountReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={modifiedData.dataExcel}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="generalLedgers.type" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "gl_type")}
                      value={
                        this.state.filter.gl_type
                          ? this.state.filter.gl_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="generalLedgers.type.select" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {gl_types.map((data, key) => (
                        <Option key={key} value={data.gl_type}>
                          {`${data.description}`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col
                    span={12}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {chart_of_accounts_reports.length === 0 &&
              !this.props.chartOfAccountReport.loading ? (
                <Empty
                  description={
                    <IntlMessages id="chartOfAccountReports.table.noData" />
                  }
                />
              ) : (
                <div className="isoInvoiceTable">
                  <Divider orientation="left" style={{ fontSize: 14 }}>
                    <IntlMessages id="details" />
                  </Divider>
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      dataSource={modifiedData.dataSource}
                      columns={columns}
                      loading={loading}
                      className="invoiceListTable forceHeight"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={false}
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    chartOfAccountReport: state.chartOfAccountReport,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(ChartOfAccountReport);
